.card {
  display: flex;
  flex-direction: column;
  background-color: var(--gray2);
  padding: 1.25rem;
  height: 52vh;
  border-radius: 1rem;
}
.card__me {
  align-self: center;
  width: 4.6875rem;
  margin-bottom: 1.875rem;
  color: var(--white);
  border-radius: 16rem;
}
.card h1 {
  display: flex;
  justify-content: center;
  font-weight: bolder;
  font-size: 1.4375rem;
  margin-bottom: 0.1875rem;
  font-weight: 700;
  color: var(--white);
}
.card h2 {
  display: flex;
  justify-content: center;
  font-size: 0.8125rem;
  margin-bottom: 0.75rem;
  font-weight: 600;
  color: var(--green);
}
.card p {
  font-weight: 400;
  font-size: 0.75rem;
  margin-bottom: 1.125rem;
  margin: auto 1.25rem auto 1.25rem;
  color: var(--white);
}
.card a,
.card a:visited,
.card a:hover {
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: var(--white);
  background: var(--gray1);
  border-radius: 0.3125rem;
  padding: 0.625rem;
  margin-bottom: 0.625rem;
  transition: 250ms ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a,
a:visited,
a:hover {
  cursor: pointer;
}
a:hover,
a:visited:hover,
a:hover:hover {
  background: var(--green);
  color: var(--gray3);
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
  min-height: 110vh;
  background: var(--gray3);
}
body .footer {
  margin-top: 2.5rem;
  color: var(--white);
}
body .footer a {
  color: var(--green);
  text-decoration: none;
  border-radius: 0.125rem;
  transition: 300ms ease-in-out;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

:root {
  --green: hsl(75, 94%, 57%);
  --white: hsl(0, 0%, 100%);
  --gray1: hsl(0, 0%, 20%);
  --gray2: hsl(0, 0%, 12%);
  --gray3: hsl(0, 0%, 8%);
}